/**
 * API
 */

const handleError = ({
    error,
    history
}) => {
    if(!error){
        return 
    }
    //
    if(!error.response) {
        alert("")
        return {
            message : ''
        }
    }
    //anauthorize
    //url
    //historyBrowserRouterRouter
    if(error.response.status === 401) {
        history.push('/login')
        return {
            message : ''
        }
    }
    return {
        message : ''
    }
}

export default handleError

